body {
  margin: 0;
}

img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  display: block;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

@font-face {
  font-family: "PT_Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/ptsans.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "PT_Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/ptsansbold.woff2") format("woff2");
  font-display: swap;
}
.description {
  margin: 0;
  padding: 0;
}

.link {
  text-decoration: none;
  color: inherit;
}

.list {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

.title {
  margin: 0;
  padding-left: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  padding: 0 18px;
}

.page {
  height: 100%;
}

.page__body {
  height: 100%;
  font-family: "PT_Sans", "Tahoma", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  margin: 0;
  min-width: 320px;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  background-color: #FFFFFF;
}

.main-nav {
  width: clamp(320px, 50%, 1280px);
  margin: 0 auto;
  font-family: "PT_Sans", "Tahoma", sans-serif;
  font-size: 18px;
  font-weight: 700;
  line-height: 26px;
  text-transform: uppercase;
}

.main-nav__button {
  margin: 0;
  padding: 0;
  border: none;
  width: 22px;
  height: 22px;
  background-image: url("../img/stack/sprite.svg#menu_burger");
  background-color: #FFFFFF;
}

.main-logo__image {
  margin: 0 auto;
}

.button {
  margin: 0;
  padding: 0;
  border: none;
  background-color: #81B3D2;
}